import React from 'react'

const ShowHop = (props) => {

    const sky=[
      null, //-1 0   ..
        props.yearSky.name,
        props.monthSky.name,
        props.daySky.name,
        props.timeSky.name
    ]

    const ground=[
      null,
        props.yearGround.name,
        props.monthGround.name,
        props.dayGround.name,
        props.timeGround.name
    ]


    //  
    const figureSkyHop=()=>{
      let skyHopArr=[];
      let i=0;
      if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
      {
        skyHopArr[i] = <div></div>;
        i++;
      }
       
      if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
      {
        skyHopArr[i] = <div></div>;
        i++;
      }

      if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
      {
        skyHopArr[i] = <div></div>;
        i++;
      }

      if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
      {
        skyHopArr[i] = <div></div>;
        i++;
      }

      if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
      {
        skyHopArr[i] = <div></div>;
        i++;
      }
      if(skyHopArr[0]==null)
      return null;
      return skyHopArr;
    }
    //  
    const figureBangHop=()=>{
      let a=ground.indexOf("");
      let b=ground.indexOf("");
      let c=ground.indexOf("");
  
      if(a!=-1&&b!=-1&&c!=-1){
        //   .
        if(ground[2]==""||ground[2]==""||ground[2]=="")
        return <div> </div>
      }

      a = ground.indexOf("");
      b = ground.indexOf("");
      c = ground.indexOf("");
      
      if(a!=-1&&b!=-1&&c!=-1){
        if(ground[2]==""||ground[2]==""||ground[2]=="")
        return <div> </div>
      }
      a = ground.indexOf("");
      b = ground.indexOf("");
      c = ground.indexOf("");
      
      if(a!=-1&&b!=-1&&c!=-1){
        if(ground[2]==""||ground[2]==""||ground[2]=="")
        return <div> </div>
      }
      
      a = ground.indexOf("");
      b = ground.indexOf("");
      c = ground.indexOf("");
      
      if(a!=-1&&b!=-1&&c!=-1){
        if(ground[2]==""||ground[2]==""||ground[2]=="")
        return <div> </div>
      }
      return null;
    }


    const figureSomHop=()=>{
      const fireHop =["","",""];
      const goldHop=["","",""];
      const waterHop =["","",""];
      const treeHop=["","",""];

      const sample1 =ground.slice(1,4);
      const sample2 = ground.slice(2,5);

     const checkFireHop1 = fireHop.filter(x=>sample1.includes(x));
     const checkFireHop2 = fireHop.filter(x=>sample2.includes(x));

    if(checkFireHop1.length==3||checkFireHop2.length==3){
        return <div>  </div>;
      }

     const checkGoldHop1=goldHop.filter(x=>sample1.includes(x));
     const checkGoldHop2 = goldHop.filter(x=>sample2.includes(x));

    if(checkGoldHop1.length==3||checkGoldHop2.length==3){
        return <div>  </div>;
      }

      const checkWaterHop1=waterHop.filter(x=>sample1.includes(x));
      const checkWaterHop2 = waterHop.filter(x=>sample2.includes(x));
 
     if(checkWaterHop1.length==3||checkWaterHop2.length==3){
         return <div>  </div>;
       }
 
       const checkTreeHop1=treeHop.filter(x=>sample1.includes(x));
       const checkTreeHop2 = treeHop.filter(x=>sample2.includes(x));
  
      if(checkTreeHop1.length==3||checkTreeHop2.length==3){
          return <div>  </div>;
        }
  
return null;
    }

    const figureBanHop=()=>{

      let banHopResult=[];
      let i=0;
      //      .
      if(!figureSomHop()){

     // 
     if(ground.includes("")){
      const fireLeft = ground.indexOf("")-1;
      const fireRight = ground.indexOf("")+1;

      if(ground[fireLeft]==""||ground[fireRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
      if(ground[fireLeft]==""||ground[fireRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
     }

     // 

     if(ground.includes("")){
      const goldLeft = ground.indexOf("")-1;
      const goldRight = ground.indexOf("")+1;

      if(ground[goldLeft]==""||ground[goldRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
      if(ground[goldLeft]==""||ground[goldRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
     }

     // 

     if(ground.includes("")){
      const waterLeft = ground.indexOf("")-1;
      const waterRight = ground.indexOf("")+1;

      if(ground[waterLeft]==""||ground[waterRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
      if(ground[waterLeft]==""||ground[waterRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
     }

     // 

     if(ground.includes("")){
      const treeLeft = ground.indexOf("")-1;
      const treeRight = ground.indexOf("")+1;

      

      if(ground[treeLeft]==""||ground[treeRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
      if(ground[treeLeft]==""||ground[treeRight]==""){
        banHopResult[i]=<div> </div>;
        i++;
      }
     }

     if(banHopResult[0]==null)
     return null;
     return banHopResult;
      }
      
      return null;


    }


    const figureSixHop=()=>{


      let sixHopResult=[];
      let i=0;

      const Za = ground.indexOf("");
      if(ground[Za-1]==""||ground[Za+1]==""){
        sixHopResult[i]=<div></div>;
        i++;
      }

      const In = ground.indexOf("");
      if(ground[In-1]==""||ground[In+1]==""){
        sixHopResult[i]=<div></div>;
        i++;
      }

      const Myo = ground.indexOf("");
      if(ground[Myo-1]==""||ground[Myo+1]==""){
        sixHopResult[i]=<div></div>;
        i++;
      }

      const Jin = ground.indexOf("");
      if(ground[Jin-1]==""||ground[Jin+1]==""){
        sixHopResult[i]=<div></div>;
        i++;
      }

      const Sa = ground.indexOf("");
      if(ground[Sa-1]==""||ground[Sa+1]==""){
        sixHopResult[i]=<div></div>;
        i++;
      }

      const Ow = ground.indexOf("");
      if(ground[Ow-1]==""||ground[Ow+1]==""){
        sixHopResult[i]=<div></div>;
        i++;
      }

      if(sixHopResult[0]==null)
      return null;
      return sixHopResult;
    }

    

  return (
    <div>
      {figureSkyHop()}
      {figureBangHop()}
      {figureSomHop()}
      {figureBanHop()}
      {figureSixHop()}
      {!figureSkyHop()&&!figureBangHop()&&!figureSomHop()&&!figureBanHop()&&!figureSixHop()&&
      <div> </div>
      }
    </div>
  )
}

export default ShowHop