import React, { useCallback, useState } from "react";
import classNames from "classnames/bind";
import styles from "./showDays.module.css";
import Logic from "../../logic/logic";
import Data from "../../data/data";
const cx = classNames.bind(styles);

const ShowDays = () => {
  const today = {
    year: new Date().getFullYear(), // 
    month: new Date().getMonth() + 1, // 
    date: new Date().getDate(), // 
    day: new Date().getDay(), // 
  };
  const week = ["", "", "", "", "", "", ""]; //
  const [selectedYear, setSelectedYear] = useState(today.year); //  
  const [selectedMonth, setSelectedMonth] = useState(today.month); //  
  const dateTotalCount = new Date(selectedYear, selectedMonth, 0).getDate(); // ,   

  const logic = new Logic();
  const data = new Data();

  const prevMonth = useCallback(() => {
    //   
    if (selectedMonth === 1) {
      setSelectedMonth(12);
      setSelectedYear(selectedYear - 1);
    } else {
      setSelectedMonth(selectedMonth - 1);
    }
  }, [selectedMonth]);

  const nextMonth = useCallback(() => {
    //   
    if (selectedMonth === 12) {
      setSelectedMonth(1);
      setSelectedYear(selectedYear + 1);
    } else {
      setSelectedMonth(selectedMonth + 1);
    }
  }, [selectedMonth]);

  const monthControl = useCallback(() => {
    //  
    let monthArr = [];
    for (let i = 0; i < 12; i++) {
      monthArr.push(
        <option key={i + 1} value={i + 1}>
          {i + 1}
        </option>
      );
    }
    return (
      <select
        onChange={changeSelectMonth}
        value={selectedMonth}
      >
        {monthArr}
      </select>
    );
  }, [selectedMonth]);

  const yearControl = useCallback(() => {
    //  
    let yearArr = [];
    const startYear = today.year - 10; //  10 
    const endYear = today.year + 10; //  10 
    for (let i = startYear; i < endYear + 1; i++) {
      yearArr.push(
        <option key={i} value={i}>
          {i}
        </option>
      );
    }
    return (
      <select
        // className="yearSelect"
        onChange={changeSelectYear}
        value={selectedYear}
      >
        {yearArr}
      </select>
    );
  }, [selectedYear]);

  const changeSelectMonth = (e) => {
    setSelectedMonth(Number(e.target.value));
  };
  const changeSelectYear = (e) => {
    setSelectedYear(Number(e.target.value));
  };

  const returnWeek = useCallback(() => {
    //  
    let weekArr = [];
    week.forEach((v) => {
      weekArr.push(
        <div
          key={v}
          className={cx(
            { weekday: true },
            { sunday: v === "" },
            { saturday: v === "" }
          )}
        >
          {v}
        </div>
      );
    });
    return weekArr;
  }, []);

  const returnDay = useCallback(() => {
    //    
    let dayArr = [];

    for (const nowDay of week) {
      const day = new Date(selectedYear, selectedMonth - 1, 1).getDay();
      const daySky = logic.returnDaySky(selectedYear,selectedMonth-1,1).name;
      const dayGround = logic.returnDayGround(selectedYear,selectedMonth-1,1).name;

      const skyNum = data.sky.findIndex(i=>i.name==daySky);
      const groundNum = data.ground.findIndex(i=>i.name==dayGround);

      

      if (week[day] === nowDay) {
        for (let i = 0; i < dateTotalCount; i++) {
          dayArr.push(
            <div
              key={i + 1}
              className={cx(
                {
                  //     
                  today:
                    today.year === selectedYear &&
                    today.month === selectedMonth &&
                    today.date === i + 1,
                },
                { weekday: true }, //  
                {
                  //  
                  sunday:
                    new Date(
                      selectedYear,
                      selectedMonth - 1,
                      i + 1
                    ).getDay() === 0,
                },
                {
                  //  
                  saturday:
                    new Date(
                      selectedYear,
                      selectedMonth - 1,
                      i + 1
                    ).getDay() === 6,
                }
              )}

            > 
              {i + 1}<br></br>
            {data.sky[i+skyNum<10?i+skyNum:(i+skyNum)%10].code}
            {data.ground[i+groundNum<12?i+groundNum:(i+groundNum)%12].code}
            </div>
          );
        }
      } else {
        dayArr.push(<div className={styles.weekday}></div>);
      }
    }

    return dayArr;
  }, [selectedYear, selectedMonth, dateTotalCount]);

  return (
    <div className={styles.container}>
      <div className={styles.title}>
        <h3>
          {yearControl()} {monthControl()}
        </h3>
        <div className={styles.pagination}>
          <button className={styles.button} onClick={prevMonth}></button>
          <button className={styles.button} onClick={nextMonth}></button>
        </div>
      </div>
      <div className={styles.week}>{returnWeek()}</div>
      <div className={styles.date}>{returnDay()}</div>
    </div>
  );
};

export default ShowDays;