import React from 'react'

const ShowChung = (props) => {

    const sky=[
        null, //-1 0   ..
          props.yearSky.name,
          props.monthSky.name,
          props.daySky.name,
          props.timeSky.name
      ]
  
      const ground=[
        null,
          props.yearGround.name,
          props.monthGround.name,
          props.dayGround.name,
          props.timeGround.name
      ]
  

    const figureSkyChung=()=>{
        let skyChungArr=[];
        let i=0;
        if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
        {
          skyChungArr[i] = <div></div>;
          i++;
        }
         
        if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
        {
          skyChungArr[i] = <div></div>;
          i++;
        }
  
        if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
        {
          skyChungArr[i] = <div></div>;
          i++;
        }
  
        if(sky.indexOf("")+1==sky.indexOf("")||sky.indexOf("")-1==sky.indexOf(""))
        {
          skyChungArr[i] = <div></div>;
          i++;
        }
        if(skyChungArr[0]==null)
        return null;
        return skyChungArr;

    }

    const figureGroundChung = ()=>{
      let groundChungArr = [];
      let i=0;

      if(ground.indexOf("")+1==ground.indexOf("")||ground.indexOf("")-1==ground.indexOf(""))
      {
        groundChungArr[i] = <div></div>;
        i++;
      }

      if(ground.indexOf("")+1==ground.indexOf("")||ground.indexOf("")-1==ground.indexOf(""))
      {
        groundChungArr[i] = <div></div>;
        i++;
      }

      if(ground.indexOf("")+1==ground.indexOf("")||ground.indexOf("")-1==ground.indexOf(""))
      {
        groundChungArr[i] = <div></div>;
        i++;
      }

      if(ground.indexOf("")+1==ground.indexOf("")||ground.indexOf("")-1==ground.indexOf(""))
      {
        groundChungArr[i] = <div></div>;
        i++;
      }

      if(ground.indexOf("")+1==ground.indexOf("")||ground.indexOf("")-1==ground.indexOf(""))
      {
        groundChungArr[i] = <div></div>;
        i++;
      }

      if(ground.indexOf("")+1==ground.indexOf("")||ground.indexOf("")-1==ground.indexOf(""))
      {
        groundChungArr[i] = <div></div>;
        i++;
      }

      if(groundChungArr[0]==null)
      return null;
      return groundChungArr;

    }

    const figureSomHyung = ()=>{
      const inSaSin = ["","",""];
      const chuckSulMe = ["","",""];

      const sample1 =ground.slice(1,4);
      const sample2 = ground.slice(2,5);

      const checkInSaSin1 = inSaSin.filter(x=>sample1.includes(x));
      const checkInSaSin2 = inSaSin.filter(x=>sample2.includes(x));

      if(checkInSaSin1.length==3||checkInSaSin2.length==3){
        return <div> </div>;
      }


      const checkChuckSulMe1 = chuckSulMe.filter(x=>sample1.includes(x));
      const checkChuckSulMe2 = chuckSulMe.filter(x=>sample2.includes(x));

      if(checkChuckSulMe1.length===3||checkChuckSulMe2.length===3){
        return <div> </div>;
      }
 
      return null;
      
    }

    const figureHyung = ()=>{
      let hyungResult = [];
      let i=0;

      if(!figureSomHyung()){
        //       


        // 
        if(ground.includes("")){
          const zaLeft = ground.indexOf("")-1;
          const zaRight = ground.indexOf("")+1;

          if(ground[zaLeft]==""||ground[zaRight]=="")
         {
          hyungResult[i]=<div></div>;
          i++;
         }
        }

        if(ground.includes("")){
          const saLeft = ground.indexOf("")-1;
          const saRight = ground.indexOf("")+1;

          if(ground[saLeft]==""||ground[saRight]=="")
          {
            hyungResult[i] = <div></div>;
            i++;
          }
        
          if(ground[saLeft]==""||ground[saRight]=="")
          {
            hyungResult[i] = <div></div>;
            i++;
          }        
        
        }

        if(ground.includes("")){
          const sulLeft = ground.indexOf("")-1;
          const sulRight = ground.indexOf("")+1;

          if(ground[sulLeft]==""||ground[sulRight]=="")
          {
            hyungResult[i] = <div></div>;
            i++;
          }
        
          if(ground[sulLeft]==""||ground[sulRight]=="")
          {
            hyungResult[i] = <div></div>;
            i++;
          }

        }

        if(hyungResult[0]==null)
        return null;
        return hyungResult;

      }



    }

  return (
    <div>
        {figureSkyChung()}
        {figureGroundChung()}
        {figureSomHyung()}
        {figureHyung()}
        </div>
  )
}

export default ShowChung